<template>
  <div id="loading-container" class="vs-con-loading__container">
    <component :is="`TrustReceiptLoanOverdue${dataLayout}`" :key="Date.now()" :parent="this"/>
  </div>
</template>

<script>
import TrustReceiptLoanOverdueListLayout from '@/views/trade/loan/trust-receipt-loan/trust-receipt-loan-overdue/trust-receipt-loan-overdue-browse/TrustReceiptLoanOverdueListLayout'
import TrustReceiptLoanOverdueCardLayout from '@/views/trade/loan/trust-receipt-loan/trust-receipt-loan-overdue/trust-receipt-loan-overdue-browse/TrustReceiptLoanOverdueCardLayout'
import TrustReceiptLoanOverduePivotLayout from '@/views/trade/loan/trust-receipt-loan/trust-receipt-loan-overdue/trust-receipt-loan-overdue-browse/TrustReceiptLoanOverduePivotLayout'
import TrustReceiptLoanOverdueTableMixin from '@/mixins/trade/loan/TrustReceiptLoanOverdueTableMixin'

export default {
  name: 'trust-receipt-loan-outstanding-browse',
  mixins: [TrustReceiptLoanOverdueTableMixin],
  components: {
    TrustReceiptLoanOverdueListLayout,
    TrustReceiptLoanOverdueCardLayout,
    TrustReceiptLoanOverduePivotLayout
  },
  data: function () {
    return {
      dataLayout: 'ListLayout'
    }
  },
  mounted: function () {
    this.loadLoanTableData()
  },
  created: function () {
    this.$eventHub.$on('toolClicked', (toolId) => { this.dataLayout = toolId })
  },
  beforeDestroy: function () {
    this.$eventHub.$off('toolClicked', (toolId) => { this.dataLayout = toolId })
  }
}
</script>
